/* Slickgrid container, including headers but ex      cluding pagination */
$container-border-top:                                0 none !default;
$container-border-right:                              0 none !default;
$container-border-bottom:                             1px solid #{$border-color} !default;
$container-border-left:                               0 none !default;

/* grid */
$grid-border-color:                                   fade(black, 3%) !default;
$grid-border-style:                                   solid !default;
$grid-header-background:                              rgba(255, 255, 255, .6) !default;
$grid-cell-color:                                     rgb(255, 255, 255) !default;

/* cell */
$cell-active-border:                                  none !default;
$cell-active-box-shadow:                              inset 0 0 0 1px #aaaaaa !default;
$cell-active-z-index:                                 6 !default;
$cell-box-shadow:                                     inherit !default;
$cell-text-color:                                     #000000 !default;
$cell-font-family:                                    $font-family !default;
$cell-font-weight:                                    normal !default;
$cell-border-top:                                     1px solid #{$border-color} !default;
$cell-border-right:                                   1px transparent !default;
$cell-border-bottom:                                  1px transparent !default;
$cell-border-left:                                    1px transparent !default;
$cell-even-background-color: 			                    #ffffff !default;
$cell-odd-background-color:                           darken($grid-cell-color, 3%) !default; // for striping every second row
$cell-odd-active-background-color:                    darken($grid-cell-color, 5%) !default;
$cell-padding-top-bottom:                             5px !default;
$cell-padding-left-right:                             6px !default;
$cell-padding:                                        $cell-padding-top-bottom $cell-padding-left-right !default;

/** 4x available slick-pane (top, bottom, left,       right) */
$slick-pane-top-border-top:                           none !default;

/* row */
$row-mouse-hover-color:                               #eff5fc !default;
$row-mouse-hover-box-shadow:                          none !default;
$row-mouse-hover-z-index:                             5 !default;
$row-selected-color:                                  #dae8f1 !default;
$row-highlight-background-color:                      darken($row-selected-color, 5%) !default;
$row-highlight-fade-animation:                        1.5s ease 1 !default;
$row-highlight-fade-out-animation:                    0.3s ease 1 !default;
$row-checkbox-selector-background:                    inherit !default;
$row-checkbox-selector-border:                        none !default;

/* Pre-Header - Header Grouping colspan */
