$multiselect-input-filter-font-family:                "Helvetica Neue", Helvetica, Arial !default;
$multiselect-input-filter-font-size:                  12px !default;
$multiselect-dropdown-border:                         1px solid #bbb !default;
$multiselect-dropdown-max-width:                      250px !default;
$multiselect-dropdown-z-index:                        9999 !default;
$multiselect-checkbox-margin-left:                    4px !default;
$multiselect-checkbox-hover-bg-color:                 $row-mouse-hover-color !default;
$multiselect-select-all-border-bottom:                1px solid #ddd !default;
$multiselect-select-all-text-color:                   darken($primary-color, 5%) !default;
$multiselect-select-all-text-hover-color:             darken($primary-color, 10%) !default;
$multiselect-icon-font-size:                          16px !default;
$multiselect-icon-arrow-font-size:                    $multiselect-icon-font-size !default;
$multiselect-icon-color:                              $primary-color !default;
$multiselect-icon-checked:                            "\f14a" !default;
$multiselect-icon-checked-color:                      $multiselect-icon-color !default;
$multiselect-icon-border:                             none !default;
$multiselect-icon-border-radius:                      none !default;
$multiselect-icon-height:                             $icon-font-size !default;
$multiselect-icon-margin:                             0 !default;
$multiselect-icon-vertical-align:                     middle !default;
$multiselect-icon-width:                              20px !default;
$multiselect-icon-unchecked:                          "\f096" !default;
$multiselect-icon-unchecked-color:                    $multiselect-icon-color !default;
$multiselect-icon-radio-color:                        $multiselect-icon-color !default;
$multiselect-icon-radio-height:                       $multiselect-icon-height !default;
$multiselect-icon-radio-width:                        $multiselect-icon-width !default;
$multiselect-icon-radio-border:                       $multiselect-icon-border !default;
$multiselect-icon-radio-border-radius:                $multiselect-icon-border-radius !default;
$multiselect-icon-radio-margin:                       $multiselect-icon-margin !default;
$multiselect-icon-radio-height:                       $icon-font-size !default;
$multiselect-icon-radio-border:                       none !default;
$multiselect-icon-radio-border-radius:                none !default;
$multiselect-icon-radio-checked:                      "\f192" !default;
$multiselect-icon-radio-unchecked:                    "\f10c" !default;
$multiselect-icon-search-margin-right:                8px !default;
$multiselect-ok-button-font-weight:                   600 !default;
$multiselect-label-margin-bottom:                     6px !default;
$multiselect-label-line-height:                       18px !default;
$multiselect-unchecked-opacity:                       0.6 !default;
$multiselect-placeholder-bg-color:                    transparent !default;
$multiselect-placeholder-color:                       $editor-placeholder-color !default;
$multiselect-placeholder-font-family:                 $filter-placeholder-font-family !default;
$multiselect-ok-button-bg-color:                      #fff !default;
$multiselect-ok-button-bg-hover-color:                darken($row-mouse-hover-color, 3%) !default;
$multiselect-ok-button-font-weight:                   600 !default;
$multiselect-ok-button-text-color:                    $primary-color !default;
$multiselect-ok-button-text-hover-color:              darken($primary-color, 5%) !default;
$multiselect-ok-button-border:                        1px solid #ccc !default;
$multiselect-ok-button-height:                        26px !default;
$multiselect-ok-button-width:                         100% !default;
$multiselect-ok-button-text-align:                    center !default;

/* pagination variables */
$pagination-border-color:                             #ddd !default;
$pagination-button-border-color:                      #acacac !default;
$pagination-button-border-radius:                     4px !default;
$pagination-button-height:                            32px !default;
$pagination-button-hover-color:                       #E6E6E6 !default;
$pagination-button-padding:                           6px 12px !default;
$pagination-button-border:                            1px solid #{$pagination-button-border-color} !default;
$pagination-border-top:                               0 none !default;
$pagination-border-right:                             0 none !default;
$pagination-border-bottom:                            0 none !default;
$pagination-border-left:                              0 none !default;
$pagination-count-margin-left:                        2px !default;
$pagination-font-size:                                calc(#{$font-size-base} - 1px) !default;
$pagination-height:                                   40px !default;
$pagination-icon-color:                               $primary-color !default;
$pagination-icon-font-size:                           calc(#{$icon-font-size} - 1px) !default;
$pagination-icon-line-height:                         calc(#{$icon-font-size} + 4px) !default;
$pagination-icon-height:                              initial !default;
$pagination-icon-seek-first:                          "\f100" !default;
$pagination-icon-seek-end:                            "\f101" !default;
$pagination-icon-seek-next:                           "\f105" !default;
$pagination-icon-seek-prev:                           "\f104" !default;
$pagination-icon-seek-first-width:                    initial !default;
$pagination-icon-seek-end-width:                      initial !default;
$pagination-icon-seek-next-width:                     initial !default;
$pagination-icon-seek-prev-width:                     initial !default;
$pagination-icon-seek-text-stroke:                    0.4px !default;
$pagination-page-input-border-radius:                 4px !default;
$pagination-page-input-bgcolor:                       #fafbed !default;
$pagination-page-input-height:                        26px !default;
$pagination-page-input-width:                         50px !default;
$pagination-page-input-padding:                       2px !default;
$pagination-page-select-border-radius:                3px !default;
$pagination-page-select-padding:                      0 0 2px 2px !default;
$pagination-page-select-height:                       32px !default;
$pagination-page-select-width:                        60px !default;
$pagination-page-select-font-size:                    calc(#{$font-size-base} - 2px) !default;
$pagination-text-color:                               #808080 !default;

/* Row Move Manager Plugin */
$row-move-plugin-icon:                                "\f0c9" !default;
$row-move-plugin-icon-width:                          $icon-font-size !default;
$row-move-plugin-size:                                $icon-font-size !default;
$row-move-plugin-cursor:                              move !default;
$row-move-plugin-icon-vertical-align:                 bottom !default;

/* selector plugin */
$selector-border-right:                               1px solid rgb(196, 196, 196) !default;

/* Viewport */
$viewport-border-top:                                 0 none !default;                      // header row is where the Filters are showing
$viewport-border-right:                               0 none !default;
$viewport-border-bottom:                              0 none !default;
$viewport-border-left:                                0 none !default;

/* Custom Footer */
$footer-bg-color:                                     transparent !default;
$footer-font-size:                                    $font-size-base !default;
$footer-font-style:                                   italic !default;
$footer-font-weight:                                  normal !default;
$footer-height:                                       30px !default; // if you modify this height, you also have to modify the footerHeight in the customFooterOptions
$footer-padding:                                      5px 0 !default;
$footer-text-color:                                   #808080 !default;
$footer-left-float:                                   left !default;
$footer-left-font-style:                              italic !default;
$footer-left-font-weight:                             normal !default;
$footer-left-padding:                                 0px !default;
$footer-left-text-align:                              left !default;
$footer-left-text-color:                              $footer-text-color !default;
$footer-left-width:                                   50% !default;
$footer-right-font-style:                             italic !default;
$footer-right-font-weight:                            normal !default;
$footer-right-padding:                                0px !default;
$footer-right-separator-margin:                       2px !default;
$footer-right-float:                                  right !default;
$footer-right-text-align:                             right !default;
$footer-right-text-color:                             $footer-text-color !default;
$footer-right-width:                                  50% !default;

/** Custom Tooltip */
$slick-tooltip-background-color:                      #ffffff !default;
$slick-tooltip-border-color:                          #BFBDBD !default;
$slick-tooltip-border:                                2px solid #{$slick-tooltip-border-color} !default;
$slick-tooltip-border-radius:                         4px !default;
$slick-tooltip-font-size:                             calc(#{$font-size-base} - 1px) !default;
$slick-tooltip-color:                                 inherit !default;
$slick-tooltip-height:                                auto !default;
$slick-tooltip-padding:                               7px !default;
$slick-tooltip-width:                                 auto !default;
$slick-tooltip-overflow:                              inherit !default;
$slick-tooltip-text-overflow:                         ellipsis !default;
$slick-tooltip-white-space:                           normal !default;
$slick-tooltip-z-index:                               9999 !default;
// tooltip arrow
$slick-tooltip-arrow-color:                           darken($slick-tooltip-border-color, 5%) !default;
$slick-tooltip-arrow-size:                            8px !default;
$slick-tooltip-down-arrow-top-margin:                 100% !default;
$slick-tooltip-up-arrow-top-margin:                   -($slick-tooltip-arrow-size * 2) !default;
$slick-tooltip-arrow-side-margin:                     9px !default;
$slick-tooltip-right-arrow-side-margin:               calc(100% - #{($slick-tooltip-arrow-size * 2)} - #{$slick-tooltip-arrow-side-margin}) !default;

/** Empty Data Warning element */
$empty-data-warning-color:                            $cell-text-color !default;
$empty-data-warning-font-family:                      $font-family !default;
$empty-data-warning-font-size:                        calc(#{$font-size-base} + 2px) !default;
$empty-data-warning-font-style:                       italic !default;
$empty-data-warning-line-height:                      18px !default;
$empty-data-warning-margin:                           0px !default;
$empty-data-warning-padding:                          8px !default;
$empty-data-warning-z-index:                          10 !default;
